@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #000000;
}
nav a {
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: 900;
  line-height: 78px;
  letter-spacing: 0em;
  margin: 2rem;
  color: white;
  text-decoration: none;
}
nav {
  width: 95%;
  right: 0;
  display: flex;
  justify-content: flex-end;
  margin-right: 3rem;
}
.App {
}
.App-header {
  padding-top: 2rem;
  margin-top: 2rem;
}
.content,
.contact {
  width: 80%;
  margin: 0 auto;
}
.content h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 5rem;
  font-style: normal;
  font-weight: 900;
  line-height: 78px;
  letter-spacing: 0em;

  color: #f47489;
}
.contact h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 5rem;
  font-style: normal;
  font-weight: 900;
  line-height: 78px;
  letter-spacing: 0em;

  color: #f8a02f;
}
.contact h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 3rem;
  font-style: normal;
  font-weight: 700;
  line-height: 78px;
  letter-spacing: 0em;
  width: 60%;
  color: #ffffff;
}
.social {
  margin-top: 2em;
  display: flex;
  flex-direction: row;
  width: 40%;
  justify-content: space-between;
}
.social img {
  width: 20%;
}

.body {
  margin-bottom: 5rem;
}
#luz {
  animation: fade-in 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) 2s both infinite;
}

#luz_2 {
  animation: fade-in 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) 1s both infinite;
}
#luz_3 {
  animation: fade-in 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) 3s both infinite;
}
@keyframes fade-in {
  0% {
    opacity: 100%;
  }
  100% {
    opacity: 0%;
  }
}
.wrapper {
  width: 80%;
  margin: 0 auto;
  position: relative;
}
.workCard {
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
  justify-content: space-between;
  flex-wrap: wrap;
}
.CardOut {
  overflow: hidden;
}
.workCard img {
  width: 21rem;
  padding: 2rem;
  border: 8px solid #ffffff;
  margin: 2rem;
}
.workInd {
  position: absolute;
  animation: slide-in-fwd-center 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.workIndOut {
  animation: slide-out-bck-center 0.5s cubic-bezier(0.55, 0.085, 0.68, 0.53)
    both;
}
.workInd img {
  width: 100%;
  display: block;
  margin: 0 auto;
  border-radius: 30px 30px;
}
.Card {
  cursor: pointer;
  position: absolute;
}
.Card:hover {
}
@keyframes slide-in-fwd-center {
  0% {
    transform: translateZ(-1400px);
    opacity: 0;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}
@keyframes slide-out-bck-center {
  0% {
    transform: translateZ(0);
    opacity: 1;
  }
  100% {
    transform: translateZ(-1100px);
    opacity: 0;
  }
}
#google-mail,
#linkedin,
#dribbeln {
  cursor: pointer;
}
.mobileHeader {
  display: none;
}
.top a {
  text-decoration: none;
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  align-items: center;
  color: white;
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: 500;
  line-height: 78px;
  letter-spacing: 0em;
  width: 95%;
  margin-right: 3rem;
}
.topFromBotton {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  align-items: center;
}
.bye {
  color: white;
  font-family: "Montserrat", sans-serif;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
}
.corazon {
  width: 2rem;
  margin-left: 1rem;
  margin-right: 1rem;
}
.byeFromMobile {
  display: none;
}
.footerMobile {
  display: none;
}
@media screen and (min-width: 1598px) {
}
@media screen and (min-width: 1260px) {
}
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (-webkit-min-device-pixel-ratio: 2) {
  body {
    width: 100%;
  }
  .headerDesktop {
    display: none;
  }
  .mobileHeader {
    display: block;
    width: 100%;
  }
  .App-header {
    padding-top: 0rem;
    margin-top: 0rem;
  }
  .content h1 {
    font-size: 3rem;
    text-align: center;
  }
  .contact {
    width: 100%;
  }
  .contact h1 {
    font-size: 3rem;
    text-align: center;
  }
  .workCard {
    width: 100%;
  }
  .topFromBotton {
    display: none;
  }
  .byeFromMobile {
    display: block;
    color: white;
    font-family: "Montserrat", sans-serif;
    text-align: center;
    width: 81%;
    font-size: 0.5rem;
    margin-left: 1rem;
    display: flex;
  }
  nav {
    display: none;
  }
  .footer {
    display: none;
  }
  .footerMobile {
    display: block;
    width: 100%;
  }
}

.workContent img {
  width: 100%;
  padding: 2rem;
  border: 4px solid white;
}
.workContent {
  position: relative;
  width: 15rem;
  height: 15rem;
  margin: 2rem;
}

.workHover {
  animation: scale-in-center 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  background: rgba(255, 255, 255, 0.6);
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  position: absolute;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  border-radius: 51%;
  width: 50%;
  height: 50%;
  cursor: pointer;
}

.lupa {
  width: 30%;
}
.workHover button {
  opacity: 1;
}
.dashboard {
  position: relative;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  width: 80%;
  margin: 0 auto;
  max-width: 1200px;
  margin: 0 auto;
  justify-content: space-between;
  flex-wrap: wrap;
}
.fullWork {
  cursor: pointer;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  border-radius: 13px 13px;
}
/* .workContent img:hover {
  border: 8px solid white;
  background-color: green;
  opacity: 100%;
} */
.workFullMobile {
  display: none;
}
@keyframes scale-in-center {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
.dashboardMob {
  display: none;
}
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (-webkit-min-device-pixel-ratio: 2) {
  .fullWork {
    display: none;
  }
  .workHover {
    display: none;
  }
  .dashboard {
    display: none;
  }
  .dashboardMob {
    display: block;
  }
  .mobileWork {
    width: 90%;
    display: block;
    margin: 0 auto;
    position: relative;
  }
  .mobileWork img {
    width: 100%;
    border: 2px solid white;
    padding: 1rem;
    margin: 0 auto;
  }
  .mobileWork button {
    font-size: 2rem;
    color: white;
    font-family: "Montserrat", sans-serif;
    background-color: #000000;
    border: 2px solid white;
    border-radius: 51%;
    font-weight: 700;
    position: absolute;
    /* padding: 0.25rem; */
    top: 10px;
    width: 3rem;
    left: 12px;
    height: 3rem;
  }
  .mobileWorkFull img {
    position: absolute;

    width: 100%;
    border-radius: 13px 13px;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
  }
  .workMobile {
    position: relative;
  }
  .mobileWorkFull button {
    font-size: 2rem;

    font-family: "Montserrat", sans-serif;
    background-color: transparent;
    border: 2px solid;
    border-radius: 51%;
    font-weight: 700;
    position: absolute;
    /* padding: 0.25rem; */
    left: 1rem;
    width: 3rem;
    bottom: 2rem;
    height: 3rem;
  }
}


